import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Routes, RouterModule} from '@angular/router';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './pages/header/header.component';
import { SideNavBarComponent } from './pages/side-nav-bar/side-nav-bar.component';
import { AboutPageComponent } from './pages/about-page/about-page.component';
import { EducationDetailsPageComponent } from './pages/education-details-page/education-details-page.component';
import { TechnologiesComponent } from './pages/technologies/technologies.component';
import { ExperienceComponent } from './pages/experience/experience.component';
import { ProjectsPageComponent } from './pages/projects-page/projects-page.component';
import { DocumentsPageComponent } from './pages/documents-page/documents-page.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { EducationService } from './pages/education-details-page/education.service';
import { DocumentService } from './pages/documents-page/document.service';

const routes: Routes = [
  { path: 'about', component: AboutPageComponent },
  { path: 'education', component: EducationDetailsPageComponent },
  { path: 'experience', component: ExperienceComponent },
  { path: 'technologies', component: TechnologiesComponent, },
  { path: 'projects', component: ProjectsPageComponent, },
  { path: 'documents', component: DocumentsPageComponent, },
  { path: '', redirectTo: '/about', pathMatch: 'full' },
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    SideNavBarComponent,
    AboutPageComponent,
    EducationDetailsPageComponent,
    TechnologiesComponent,
    ExperienceComponent,
    ProjectsPageComponent,
    DocumentsPageComponent,
    PageNotFoundComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot(routes)
  ],
  providers: [EducationService, DocumentService],
  bootstrap: [AppComponent]
})
export class AppModule { }
